import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import { Pagination, Navigation } from 'swiper/modules';

import './css/card.css';

//import { acomodacoes } from '../backend/dados'

const Card = ({dados}) => {

    function getRandomInt(min,max){
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor (Math.random() * (max - min +1)) + min;
    }

    function animar(e){
        e.target.classList.toggle('animate')
    }

    if(dados.length == 0){
        return(
            <div style={{paddingTop:'180px'}} className='d-flex justify-content-center align-items-center'>
                <div className='container-airbnb row'>
                    <div className='col mt-5'>
                        <div className='card text-center'>
                            <div className='card-body'>
                                Nenhuma acomodao encontrada para esta categoria 
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    } else{
        return (
            <div className='container-fluid'>
                <div style={{paddingTop:'180px', paddingBottom: '100px'}} className="container-airbnb row">
                    {
                        dados.map((acomodacao, index) =>(
                            <div key={acomodacao.id} className="col-xxl-2 col-lg-3 col-md-4 col-sm-6 position-relative mt-4">
                                <div onClick={animar} className='heartAnimation position-absolute top-0 end-0'></div>
                                <Swiper
                                className='img-content'
                                    pagination={true}
                                    navigation={true}
                                    modules={[Pagination, Navigation]}
                                >
                                    {
                                        acomodacao.imagens.map((imagem, index) => (
                                        <SwiperSlide 
                                            key={index} className='swiperImg'
                                        >
                                            <img src={imagem} className='img-fluid cardImage'></img>
                                         </SwiperSlide>
                                        ))
                                    }
                                </Swiper>    
                                
                                <p className='d-flex justify-content-between mt-3 mb-0'>
                                    <span style={{display:'block', maxWidth: '170px'}} className='fw-bold text-truncate'>{acomodacao.cidade}, {acomodacao.pais}</span>
                                    <span><i className='mdi mdi-star'></i>{acomodacao.nota}</span>
                                </p>
                                <p className='text-muted my-0'>{getRandomInt(10,500)}KM de distancia</p>
                                <p className='text-muted'>{getRandomInt(0,31)} de Out - {getRandomInt(0,31)} de novembro</p>
                                <p className='fw-bold'>R$ {acomodacao.preco.toLocaleString('pt-BR')} noite</p>
        
                            </div>
        
                        ))
                    }
                    
        
                </div>
            </div>
          )

    }

 
}

export default Card;
