import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap';
import "./app.css";
import { useState, useEffect } from 'react';

import Navbar from './components/Navbar';
import Categorias from './components/Categorias';
import ModalFilter from './components/ModalFilter';
import Card from './components/Card';

import { Modal } from 'bootstrap/dist/js/bootstrap';
import Skeleton from './components/Skeleton';

function App() {

  const [isLoadoing,setIsLoading] = useState(true);
  const [catId,setCatId] = useState(1);
  const [allHouses,setAllHouses] = useState([]);
  const [filterHouses, setFilterHouses] = useState([]);

  //primeira vez que a aplicao rodou
  useEffect(() =>{
    fetch('https://curso.programacaoweb.com.br/api-airbnb/')
    .then((resposta) => {
      if (resposta.ok){
        return resposta.json();
      }
      throw new Error('Algo deu errado')
    }).then ((respostaJSON) =>{
      setAllHouses(respostaJSON);
      setIsLoading(false);
    }).catch((error)=>{
      console.log(error)
    })
  }, [])

  //Mostra o que foi puxado da api e em seguida filtra usando a funo
  useEffect(() => {
    filterById(catId);
  }, [allHouses])

  //Mostra o array da api aps o filtro
  useEffect(() => {
  }, [filterHouses])

  //Funo para receber id das categorias
  const changeCat = (id) => {
    setCatId(id);
    filterById(id);
  }

  //funao que filtra o array vindo da Api baseado no id
  const filterById = (id) =>{
    const novaLista = allHouses.filter((item)=>{
      return item.categoria === id;
    })
    setFilterHouses(novaLista);
  }
  //Funao para filtrar por preo selecionado no priceSlider
  const filterByPrice = (catId, min, max) => {
    const novaLista = allHouses.filter((item)=>{
      return item.categoria === catId
      && item.preco >= min
      && item.preco <= max
    })
    setFilterHouses(novaLista);
  }

  const resetFilter = (id) => {
    filterById(id);

  }

  return (
    <>
      <Navbar />
      <Categorias changeCat={changeCat} />

      {
        isLoadoing ?
        <div className='container-airbnb row' style={{paddingTop: '180px'}}>
          <Skeleton />
          <Skeleton />
          <Skeleton />
          <Skeleton />
          <Skeleton />
          <Skeleton />
        </div>
        :  <Card dados={filterHouses} />
      }
      <ModalFilter resetFilter={resetFilter} catId={catId} filterByPrice ={filterByPrice} itens={filterHouses}/>
    </>
  );
}

export default App;
